/* Global */

* {
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

body {
  background-color: #576e7c;
}

/* Chats Page */

.chats-page {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
}

.nav-bar {
  position: sticky;
  width: 100%;
  height: 66px;
  background-color: #144ca7f5;
}

.logo-tab {
  position: absolute;
  left: 22px;
  top: 12px;
  font-size: 32px;
  font-weight: 700;
  color: white;
}

.logout-tab {
  position: absolute;
  top: 22px;
  right: 22px;
  color: white;
  cursor: pointer;
  font-size: medium;
}

.profile-section {
  position: absolute;
  right: 100px;
  top: 23.3px;
  font-size: medium;
  color: white;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

/* Login Page */

#login-page {
  background-image: linear-gradient(#40a9ff33, #096dd9);
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}

#login-card {
  display: flex;
  position: relative;
  top: calc(50vh - 144px);
  left: calc(50vw - 210px);
  padding-top: 36px;
  padding-bottom: 66px;
  width: 420px;
  text-align: center;
  background-color: white;
  border-radius: 22px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
}

.login-button {
  cursor: pointer;
  color: white;
  padding: 12px;
  margin-top: 1rem;
  border-radius: 8px;
  display: inline-block;
  background-color: #4285f4;
  color: white;
}

#loading-container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgb(255, 255, 255, 0.22);
}

#loading-icon {
  font-size: 66px;
  position: relative;
  left: calc(50vw - 33px);
  top: calc(50vh - 22px);
}

/* Profile Page */

.profile-page {
  text-align: center;
  background-color: #E4F9F5;
  display: grid;
  background-image: linear-gradient(#40a9ff33, #096dd9d2);
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}

.profile-page img {
  border-radius: 50%;
  width: 180px;
}

.profile-page p {
  font-weight: 500;
  color: white;
}

.middle-container {
  margin-top: 50px;
  margin-bottom: 50px;
  font-size: larger;
}

.form__field {
  font-family: inherit;
  width: 75%;
  border: 0;
  border-bottom: 2px solid #9b9b9b;
  outline: 0;
  font-size: 1.3rem;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
}

.form__field:focus ~ .form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: #11998e;
  font-weight:700;    
}

.form__field:focus {
  padding-bottom: 6px;  
  font-weight: 700;
  border-width: 3px;
  border-image: linear-gradient(to right, #11998e, #38ef7d);
  border-image-slice: 1;
}

/* reset input */
.form__field:required, .form__field:invalid {
  box-shadow:none;
}

.container {
  float: right;
  height: 80%;
  width: 80%;
}

.content {
  height: 100%;
  overflow: auto;
  background-color: #E4F9F5;
}

.rce-sbar {
  background-color: #282C34 !important;
}

.rce-container-citem div {
  background-color: #E4F9F5;
}

.form-input-1 {
  width: 79.5%;
  float: right;
  background-color: #E4F9F5;
  height: 11%;
  padding-left: 0.5%;
}

.form-input-1 div {
  background-color: #E4F9F5;
}

input.rce-input {
  color: #E4F9F5;
}

button.rce-button {
  height: 40px;
}

.typing {
  display: none;
}

.join-room {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  border-radius: 5px;
  cursor: pointer;
  padding: 8px;
  text-align: center;
  box-sizing: border-box;
  background: #1D53AA;
  color: white;
  transition: all 0.15s ease;
  user-select: none;
  border: none;
  outline: none;
  border: none;
  position: relative;
}
